import React, { useState, useEffect } from 'react';
import { ReactComponent as Atom } from '../assets/Atom.svg';
import styles from './App.module.scss';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSearch } from '@fortawesome/free-solid-svg-icons';
import Card from 'components/Card/Card';

const initUrl = 'https://yts.mx/api/v2/list_movies.json?limit=50&query_term=';

/* -------------------------------------------------------------------------- */

function App() {
  // APIÎ°úÎ∂ÄÌÑ∞ ÎπÑÎèôÍ∏∞ ÏöîÏ≤≠ÏúºÎ°ú ÏùëÎãµ Î∞õÏùÑ Îç∞Ïù¥ÌÑ∞Î•º Î≥¥Í¥ÄÌï† ÏÉÅÌÉú
  const [movieData, setMovieData] = useState([]);
  // ÏóêÎü¨ ÏÉÅÌÉú
  const [hasError, setHasError] = useState(null);
  // Î°úÎî© ÏÉÅÌÉú Í¥ÄÎ¶¨
  const [isLoading, setIsLoading] = useState(true);
  // input value Í¥ÄÎ¶¨
  const [keyword, setKeyword] = useState('');
  // get ÏöîÏ≤≠ Ï†ÑÏÜ°ÏùÑ ÏúÑÌïú Í≤ÄÏÉâÏñ¥ Í¥ÄÎ¶¨
  const [movieUrl, setMovieUrl] = useState(initUrl);

  const handleKeyword = e => {
    setKeyword(e.target.value);
  };

  const handleSearch = e => {
    if (e.target.matches('button') || e.key === 'Enter') {
      if (keyword === '') return;
      setMovieUrl(initUrl + keyword);
      setKeyword('');
      setIsLoading(true);
    } else return;
  };

  // side effect
  // Î™ÖÎ†πÌòï, Íµ¨ÎèÖ(Ï∑®ÏÜå), ÎπÑÎèôÍ∏∞(ÎÑ§Ìä∏ÏõåÌÅ¨) Îç∞Ïù¥ÌÑ∞ Ìå®Ïπò, ...
  useEffect(
    () => {
      // ÎπÑÎèôÍ∏∞ ÏöîÏ≤≠
      fetch(movieUrl)
      .then(response => response.json()) // resolved
      .then(res => {
        setMovieData(res.data.movies);
        setIsLoading(false);
      }) // ÌïÑÏöîÌïú Îç∞Ïù¥ÌÑ∞Îßå ÎîîÏä§Ìä∏Îü≠Ï≤òÎßÅ Ìï†ÎãπÏúºÎ°ú ÎΩëÏïÑÎÇ¥Ïñ¥ Ï≤òÎ¶¨ÌïòÍ∏∞
      .catch(
        // rejected
        (error) => {
          setHasError(error);
        }
      );
    }, [movieUrl]
  );
  // useEffectÎäî Ìï®ÏàòÎ•º Î∞òÌôòÌïòÎäîÎç∞, Ïù¥Î•º clean up Ìï®ÏàòÎùºÍ≥† Ìï®.
  // "Îí∑Ï≤òÎ¶¨ Ìï®Ïàò" üôÇ -> Î©îÎ™®Î¶¨ ÎÇ≠ÎπÑÎ•º Î∞©ÏßÄÌïòÍ∏∞ ÏúÑÌïú!
  // ex) remove event listener

  if (isLoading) {
    return <Atom />
  }

  if (hasError) { // ÏóêÎü¨Í∞Ä Î∞úÏÉùÌïú ÏÉÅÌô©ÏóêÏÑú Î†åÎçîÎßÅ
    return <div role="alert">{hasError.message}</div>
  }

  // Ï†ïÏÉÅÏ†ÅÏúºÎ°ú ÏöîÏ≤≠Ïù¥ Ï≤òÎ¶¨ÎêòÏñ¥ ÏùëÎãµÏù¥ Ïò® Í≤ΩÏö∞ Î†åÎçîÎßÅ
  return (
    <div className={styles['App']}>
      <h1 className={styles['h1']}>MYÏòÅÌôîÍ¥Ä</h1>
      <div className={styles['searchContainer']}>
        <div className="search-form">
        {/* <form> */}
        {/* <form action={movieUrl} method="get" className="search-form"> */}
          <input
            type="text"
            name="searchMovie"
            value={keyword}
            id="searchMovie"
            className={styles['search']}
            placeholder="Ïñ¥Îñ§ ÏòÅÌôîÍ∞Ä Î≥¥Í≥†Ïã∂ÏúºÏÑ∏Ïöî?"
            onChange={handleKeyword}
            onKeyUp={handleSearch}
          />
          <button
            className={styles['searchBtn']}
            // type="submit"
            type="button"
            onClick={handleSearch}
          >
            <FontAwesomeIcon icon={faSearch} />
          </button>
        </div>
        {/* </form> */}
      </div>

      <div className="newsArea" lang="ko">
        <ul className={styles['container']}>
          <Card data={movieData} />
        </ul>
      </div>
    </div>
  )
}

export default App
